/* CSS Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    background-color: #121212;
    font-family: Arial, sans-serif;
}

.task-essence {
    padding: 0 5%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.ep {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
    background-color: #6c63ff;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.task-avatar {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #2c2c2c;
    color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-right: 15px;
    flex-shrink: 0;
}
.ep h3{
    margin-left: 4%;
}
.essential {
    padding: 10px 20px;
    font-size: 1rem;
    font-weight: bold;
    background-color: #333;
    color: #fff;
    border-radius: 25px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}
.essential:hover {
    background-color: #555;
    transform: scale(1.05);
}
.task-details {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.task-details h3 {
    font-size: 1.2rem;
    margin: 0;
    color: #fff;
}


.ep:hover {
    transform: scale(1.02);
}
.buttons
h3 {
    color: white;
    font-size: 1.2rem;
}



.essential:hover {
    background-color: #444;
}

