
#root {
  max-width: 1000px;
  margin: 0;
  height: 100vh;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: black;
  color: #FAFAFA;
  overflow: hidden;
}

nav {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: black;
  border-top:solid 0.5px white; ;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

nav ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin: 0 20px;
  justify-content: center;
  align-items: center;
}

nav ul li a svg {
  color: #6c63ff;
  margin-left: 18%;
}
.g-box{
  justify-content: center;
  align-items: center;
  margin-top: 48%;
}
.general-container {
  text-decoration: none;
  margin-top: 2%;
  justify-content: center;
  align-items: center;
  /* margin-left: 8%; */
}

.home-container {
  text-decoration: none;
}

.username {
  display: flex;
  color: white;
  margin-top: 10px;
  margin-left: 20px;
}

#coin img {
  width: 10%;
  height: 10%;
  border-radius: 50px;
  margin-top: 10px; 

  /* image margin sha */
}

.coin {
  font-size: 2rem;
}

.title {
  background-color: #FFFFED;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333;
}

.clant {
  margin-left: 4%;
  margin-top: 3%;
  font-size: 1.2rem;
}

.balance {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 20%;
  margin-right: 20%;
  margin-top: 4%;
  text-align: center;
  border-radius: 8px;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #333;
}

.buttonss {
  margin-top: 16%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: none;
}

.increment-feedback {
  position: absolute;
  font-size: 24px;
  font-weight: bold;
  cursor: none;
  color: #FFD700;
  /* Gold */
  animation: floatUp 0.5s ease-out;
}

@keyframes floatUp {
  0% {
    opacity: 1;
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    transform: translateY(-20px);
  }
}

.tap-button {
  background-image: url('https://i.ibb.co/SvFLsDC/logo.png');
  background-size: cover;
  background-position: center;
  /* cursor:pointer; */
  cursor: none;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: none;
}

.range {
  background-color: #333;
  margin-top: 5%;
  padding: 5px 5px;
  width: 50%;
  text-align: center;
  border-radius: 25px;
}

.join-general{
  margin-left: 0;
}

.join {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 1rem;
}

.join button {
  display: flex;
  align-items: flex-start;
  justify-content: start;
  padding: 10px 15px;
  background-color: #6c63ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}


.join button a {
  color: inherit;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 0.2rem;
}

.join button:hover {
  background-color: #5848c2;
  /* Darker hover color */
}

/* Button styling */
.alone {
  background-color: #6c63ff;
  border: none;
  width: 16em;
  padding: 20px 20px;
  border-radius: 4px;
  margin-top: 20%;
}

/* Streak container */
.streak-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20%;
  margin-left: 20%;
}
.streak-container h1{
  font-size: 2rem;
}
/* Login streak styling */
#login-streak {
  margin-top: 20%;
}
.streak-container button{
  margin-top: 60%;
  padding: 12px;
  width: 100%;
}
.referral-container{
  display: flex;
  flex-direction: column;
  padding: 10px;
  margin-left: 12%;
  margin-right: 10%;
  font-family: Arial,sans-serif ;
}
.referral-container h2{
  font-size: 2rem;
  margin-top: 10%;
  text-align: center;
}
.referral-container p{
  font-size: 1.5rem;
  margin-top: 5%;
  margin-bottom: 2.5%;
}
.referral-container h4{
  font-size: 1rem;
  margin-top: 5%;
  margin-bottom:5%;
}
.referred-users-container{
  background-color: #333;
  border-radius: 2%;
  padding-left: 4px;
  padding-right: 4px;
  margin-top: 4%;
  border: solid 1px #FAFAFA ;
}
.referred-users{
  font-size: 1rem;
}
.referred-users h4{
  font-size: 0.5rem;
}
.referral-link{
  width: 100%;
  padding: 8px;
  font-size: 14px;
}
.copytoclipboard{
  color: white;
  padding: 8px 12px;
  background-color: #007BFF;
  cursor: pointer;
  border: none;
  margin-top: 2%;
}
.refer{
  display: flex;
  flex-direction: column;
  width: 100% ;
  padding-left: 2px;
  margin: 0;
  border: none;
  color: white;
}
.refer ul{
  list-style-type: none;
  border: 1px solid white;
}
.refer ul li {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  padding: 5px;
  border-radius: 5px;
}
.some-tasks {
  margin-top: 10%;
  display: flex;
  flex-direction: column;
  margin-left: 4%;
}
.task-heading {
  text-align: center;
  margin-bottom: 10px;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.task {
  font-size: 2rem;
  color: white;
  margin: 0;
}

.task-buttons{
  display: flex;
  justify-content: space-around;
  background-color: black;
  align-items: center;
  padding-top: 4px;
  padding-bottom: 4px;
  width: 100%;
  border-radius: 4px;
}
.task-buttons button {
  padding: 8px 12px;
  font-size: 0.9rem;
  font-weight: bold;
  background-color: black;
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.modal{
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-content{
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
}
.modal-content h3{
  margin-top: 4px;
  margin-bottom: 2px;
}
.modal-content p{
  margin-top: 4px;
  margin-bottom: 8px;
}
.wallet-container{
  margin-top: 10%;
  display: flex;
  margin-left: 30%;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
}
.wallet-container h3{
  margin-top: 40%;
  font-size: 1.5rem;
  text-align: center;
}